import './view.css';
import AppController from '../controller/AppController';


export default class Landing {
  constructor() {
    window.console.log('Landing created');
  }

  async start() {
    window.console.log('Landing started');
    this.test = '';

    const ButtonSignIn = document.getElementById('button-signin');
    const email = document.getElementById('email');
    const askEmail = document.getElementsByClassName('ask-email');
    // const formSignIn = document.getElementById('form-signin');
    ButtonSignIn.addEventListener('click', () => {
      if (!email.value) {
        // askEmail[0].classList.remove('ask-email');
        askEmail[0].style.visibility = 'visible';
      } else {
        const pattern = /^\w+@\w+\.\w{2,4}$/i;
        if (pattern.test(email.value)) {
          delete localStorage.email;
          localStorage.setItem('email', email.value);
          const url = './view.html';
          const newWin = window.open(url);
          newWin.onload = () => {
          // window.location.assign(url);
            const appController = new AppController(newWin);
            appController.start();
          };
        }
      }
      // window.location.replace('http://localhost:8080/views/view.html');
    });
  }
}
