/* eslint-disable max-len */
/* eslint-disable no-param-reassign */
/* eslint-disable no-plusplus */
/* eslint-disable prefer-destructuring */
import View from '../views/view';
import GIF from './gif';
import '../views/view.css';

export default class AppController {
  constructor(win) {
    this.win = win;
  }

  async start() {
    const newWindow = this.win;
    const that = this;
    this.view = new View(newWindow);
    this.view.init();
    const leftPanel = newWindow.document.getElementById('left-panel');
    const tempCanvas = document.createElement('canvas');
    tempCanvas.width = 640;
    tempCanvas.height = 640;
    let isDrawing = false;
    let mouseDownX = 0;
    let mouseDownY = 0;
    let x = 0;
    let y = 0;

    const imgPen = newWindow.document.getElementById('img-pen');
    const imgBucket = newWindow.document.getElementById('img-bucket');
    const colors = newWindow.document.getElementById('colors');
    const canvasResize = newWindow.document.getElementById('canvas-resize');
    const colorCircle = newWindow.document.getElementById('current-color-circle');
    const previousColorCircle = newWindow.document.getElementById('previous-color-circle');
    const addFrame = newWindow.document.querySelector('.add-frame');
    const canvasPlayer = newWindow.document.getElementById('canvas-player');
    const canvasDiv = newWindow.document.querySelector('div.canvas');
    const domFrames = [];
    const domCanvas = [];
    const exportImg = newWindow.document.getElementById('save-img');
    const selectCanvasSize = newWindow.document.getElementsByClassName('canvas-size');
    let currentColor = newWindow.getComputedStyle(colorCircle).backgroundColor;
    const rangeSizeCanvas = newWindow.document.getElementById('range');
    let colorLayer;
    let count = 0;
    let currentFrame = 1;
    let dragedFrameId;
    let fps = 5;

    let currentCanvas = newWindow.document.getElementById('empty-canvas');
    let context = currentCanvas.getContext('2d');
    let rect = currentCanvas.getBoundingClientRect();
    let sizeOrder = 1;
    const currentOption = selectCanvasSize[0][sizeOrder];
    currentOption.checked = true;
    let canvasSize = Number(currentOption.value);
    let previousCanvasSize = canvasSize;
    let cnt = 0;
    let intervalID = 0;
    let User = {};
    const canvasPlayerContext = newWindow.document.getElementById('canvas-player').getContext('2d');

    const returnObj = JSON.parse(localStorage.getItem(localStorage.email));
    if (!returnObj) {
      User = {
        email: localStorage.email,
        canvasSize,
        currentFrame,
        currentColor,
      };
    } else {
      sizeOrder = Math.floor(returnObj.canvasSize / 64);
      selectCanvasSize[0][sizeOrder].checked = true;
      currentFrame = returnObj.currentFrame;
      colorCircle.style.backgroundColor = returnObj.currentColor;
      User = {
        email: localStorage.email,
        canvasSize,
        currentFrame,
        currentColor: colorCircle.style.backgroundColor,
      };
    }
    selectCanvasSize[0][sizeOrder].selected = true;

    function matchStartColor(pixelPos, startPixel) {
      const r = colorLayer.data[pixelPos];
      const g = colorLayer.data[pixelPos + 1];
      const b = colorLayer.data[pixelPos + 2];
      return (r === startPixel[0] && g === startPixel[1] && b === startPixel[2]);
    }

    function colorPixel(pixelPos) {
      const rgbaString = currentColor.replace(/[a-z () '"]/g, '');
      const rgba = rgbaString.split(',');
      colorLayer.data[pixelPos] = rgba[0];
      colorLayer.data[pixelPos + 1] = rgba[1];
      colorLayer.data[pixelPos + 2] = rgba[2];
      colorLayer.data[pixelPos + 3] = 255;
    }

    // handler for color picker
    colors.addEventListener('click', (e) => {
      let { target } = e;
      while (!target.className.includes('predifined-color')) {
        target = target.parentNode;
      }
      if (newWindow.document.body.className.includes('cursor-color-picker')) {
        currentColor = newWindow.getComputedStyle(colorCircle).backgroundColor;
        previousColorCircle.style.backgroundColor = currentColor;
        colorCircle.style.backgroundColor = newWindow.getComputedStyle(target).backgroundColor;
      }
    });

    // handler for scaling canvas
    canvasResize.addEventListener('change', (e) => {
      const { target } = e;
      previousCanvasSize = canvasSize;
      target.options[target.options.selectedIndex].checked = true;
      canvasSize = Number(target.value);
      User.canvasSize = canvasSize;
      let imgData;

      domCanvas.forEach((item) => {
        const canvasContext = item.getContext('2d');
        const newCanvas = newWindow.document.createElement('canvas');
        if ((previousCanvasSize / canvasSize) > 1) {
          imgData = canvasContext.getImageData(160, 160, 320, 320);
          newCanvas.width = 320;
          newCanvas.height = 320;
        } else {
          imgData = canvasContext.getImageData(0, 0, 640, 640);
          newCanvas.width = 640;
          newCanvas.height = 640;
        }

        const scale = previousCanvasSize / canvasSize;
        const contextNewCanvas = newCanvas.getContext('2d');
        contextNewCanvas.putImageData(imgData, 0, 0);

        if (scale < 1) {
          const pos = (640 - 640 / (1 / scale)) / 2;
          window.console.log(pos);
          canvasContext.clearRect(0, 0, 640, 640);
          canvasContext.drawImage(newCanvas, pos, pos, 640 * scale, 640 * scale);
        } else {
          canvasContext.clearRect(0, 0, 640, 640);
          canvasContext.drawImage(newCanvas, 0, 0, 320 * scale, 320 * scale);
        }

        contextNewCanvas.clearRect(0, 0, newCanvas.width, newCanvas.height);
      });

      domFrames.forEach((frame, i) => {
        const frameContext = frame.getContext('2d');
        const canvasforFrame = newWindow.document.getElementById(`canvas-main${i + 1}`);
        frameContext.clearRect(0, 0, frame.width, frame.height);
        frameContext.drawImage(canvasforFrame, 0, 0, frame.width, frame.height);
      });
    });

    // function for color filling
    function colorFilling(startX, startY) {
      const pixelStack = [[startX, startY]];
      colorLayer = context.getImageData(0, 0, currentCanvas.width, currentCanvas.height);
      const pixelData = context.getImageData(startX, startY, 1, 1).data;

      while (pixelStack.length) {
        let pixelPos; let reachLeft; let reachRight;
        const newPos = pixelStack.pop();
        const coordX = newPos[0];
        let coordY = newPos[1];

        pixelPos = Math.floor((coordY * currentCanvas.width + coordX)) * 4;
        while ((coordY >= 0) && matchStartColor(pixelPos, pixelData)) {
          pixelPos -= currentCanvas.width * 4;
          coordY -= 1;
        }
        pixelPos += currentCanvas.width * 4;
        ++coordY;
        reachLeft = false;
        reachRight = false;
        while ((coordY < (currentCanvas.height - 1)) && matchStartColor(pixelPos, pixelData)) {
          colorPixel(pixelPos);

          if (coordX > 0) {
            if (matchStartColor(pixelPos - 4, pixelData)) {
              if (!reachLeft) {
                pixelStack.push([coordX - 1, coordY]);
                reachLeft = true;
              }
            } else if (reachLeft) {
              reachLeft = false;
            }
          }

          if (coordX < (currentCanvas.width - 1)) {
            if (matchStartColor(pixelPos + 4, pixelData)) {
              if (!reachRight) {
                pixelStack.push([coordX + 1, coordY]);
                reachRight = true;
              }
            } else if (reachRight) {
              reachRight = false;
            }
          }

          pixelPos += currentCanvas.width * 4;
          coordY += 1;
        }
      }
      context.putImageData(colorLayer, 0, 0);
      const currentFr = newWindow.document.getElementById(`canvas${currentFrame}`);
      const currentFrCanvCntx = currentFr.getContext('2d');
      currentFrCanvCntx.clearRect(0, 0, currentFr.width, currentFr.height);
      currentFrCanvCntx.drawImage(currentCanvas, 0, 0, currentFr.width, currentFr.height);
    }

    function leftPanelEventClick(evt) {
      let { target } = evt;

      while (!target.className.includes('instrument')) {
        target = target.parentNode;
      }

      if (target.className.includes('bucket')) {
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-bucket');
      } else if (target.className.includes('dropper')) {
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-color-picker');
      } else if (target.className.includes('line')) {
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-line');
      } else if (target.className.includes('rectangle')) {
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-rectangle');
      } else if (target.className.includes('pen')) {
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-pen');
      } else if (target.className.includes('eraser')) {
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-eraser');
      }
    }

    function leftPanelEventMouseOver(evt) {
      let { target } = evt;
      if (target) {
        while (!target.className.includes('instrument')) {
          target = target.parentNode;
        }
        target.classList.add('left-panel-bg');
      }
    }

    function leftPanelEventMouseOut(evt) {
      let { target } = evt;

      while (!target.className.includes('instrument')) {
        target = target.parentNode;
      }
      target.classList.remove('left-panel-bg');
    }

    // function for updating frame number and frame, instrument and canvas in frame id
    function updateFrames() {
      domFrames.forEach((item, i) => {
        const instruments = item.nextElementSibling;
        const frameNumber = instruments.childNodes[0];
        frameNumber.innerHTML = i + 1;
        instruments.id = `instruments${i + 1}`;
        const frameItem = item.closest('.frame-item');
        frameItem.id = `frame-item${i + 1}`;
        item.id = `canvas${i + 1}`;
      });
    }

    // function checks frames amount and hide (for one frame) or
    // show (for more than one frames) delete and darg- instruments
    function checkFrames(newFrameNumber) {
      if (domFrames.length === 1) {
        const oneFrame = newWindow.document.getElementsByClassName('frame-item');
        oneFrame[0].classList.add('click-frame');
        currentFrame = 1;
        const instruments = oneFrame[0].lastChild;
        instruments.childNodes.forEach((item) => {
          if (item.className.includes('drag-frame') || item.className.includes('delete-frame')) {
            item.classList.add('instrument-hidden');
          }
        });
      } else {
        domFrames.forEach((item) => {
          if (item.parentNode.id === `frame-item${newFrameNumber}`) {
            item.parentNode.classList.add('click-frame');
            item.parentNode.classList.add('mouseover-frame');
            currentFrame = newFrameNumber;
          } else {
            item.parentNode.classList.remove('click-frame');
            item.parentNode.classList.remove('mouseover-frame');
          }
          item.parentNode.childNodes.forEach((el) => {
            if (el.className.includes('instruments')) {
              el.childNodes.forEach((elem) => {
                if (elem.className.includes('drag-frame') || elem.className.includes('delete-frame')) {
                  elem.classList.remove('instrument-hidden');
                }
              });
            }
          });
        });
      }
    }

    // function manage visibility for main canvases
    function changeMainCanvasVisibility() {
      domCanvas.forEach((item) => {
        if (item !== currentCanvas) {
          item.style.visibility = 'hidden';
          item.style.zIndex = 0;
        } else {
          item.style.visibility = '';
          item.style.zIndex = 1;
        }
      });
    }

    function startAnimation(numberFrames) {
      cnt = 0;
      const intensity = Math.floor(1000 / numberFrames);
      if (intervalID) {
        clearInterval(intervalID);
      }
      intervalID = setInterval(() => {
        if (cnt === domCanvas.length) {
          cnt = 0;
        }
        const canvas = domCanvas[cnt];
        canvasPlayerContext.clearRect(0, 0, canvasPlayer.width, canvasPlayer.height);
        canvasPlayerContext.drawImage(canvas, 0, 0, canvasPlayer.width, canvasPlayer.height);

        cnt += 1;
      }, intensity);
    }

    // function for deleting frame and canvas
    function deleteHandler(event) {
      let target;
      if (!event) {
        target = newWindow.document.getElementById(`frame-item${currentFrame}`);
      } else {
        target = event.currentTarget;

        while (!target.classList.contains('frame-item')) {
          target = target.parentNode;
        }
      }
      const removeCanvas = newWindow.document.getElementById(`canvas${target.id.slice(-1)}`);
      domFrames.forEach((item, i) => {
        if (item === removeCanvas) {
          domFrames.splice(i, 1);
          domCanvas.splice(i, 1);
        }
      });
      // deleting frame
      const parent = target.parentNode;
      parent.removeChild(target);
      updateFrames();
      count -= 1;
      checkFrames(count);
      // deleting main canvas
      const canvasRemove = newWindow.document.getElementById(`canvas-main${target.id.slice(-1)}`);
      canvasDiv.removeChild(canvasRemove);
      domCanvas.forEach((item, i) => {
        item.id = `canvas-main${i + 1}`;
      });
      currentCanvas = newWindow.document.getElementById(`canvas-main${count}`);
      changeMainCanvasVisibility();
      clearInterval(intervalID);
      startAnimation(fps);
      event.stopPropagation();
    }

    function currentCanvasHandlers() {
      currentCanvas.addEventListener('click', (e) => {
        let startX = e.clientX - rect.left;
        let startY = e.clientY - rect.top;

        if (newWindow.document.body.className.includes('cursor-bucket')) {
          startX += imgBucket.width;
          startY += imgBucket.height;
        }
        const pixelData = context.getImageData(startX, startY, 1, 1).data;

        if (newWindow.document.body.className.includes('cursor-color-picker')) {
          const rgba = `rgba(${pixelData[0]}, ${pixelData[1]}, ${pixelData[2]}, ${pixelData[3]})`;
          previousColorCircle.style.backgroundColor = currentColor;
          colorCircle.style.backgroundColor = rgba;
        } else if (newWindow.document.body.className.includes('cursor-bucket')) {
          colorFilling(startX, startY);
        }
      });

      // Add the event listeners for mousedown, mousemove, and mouseup
      currentCanvas.addEventListener('mousedown', (e) => {
        if (newWindow.document.body.className.includes('cursor-pen') || newWindow.document.body.className.includes('cursor-eraser') || newWindow.document.body.className.includes('cursor-line') || newWindow.document.body.className.includes('cursor-rectangle')) {
          x = e.clientX - rect.left;
          y = e.clientY - rect.top + imgPen.height;
          x = Math.floor(x / 10) * 10;
          y = Math.floor(y / 10) * 10;
          isDrawing = true;
          if (newWindow.document.body.className.includes('cursor-line')
          || newWindow.document.body.className.includes('cursor-rectangle')
          ) {
            mouseDownX = x;
            mouseDownY = y;
            const imgData = context.getImageData(0, 0, 640, 640);
            tempCanvas.getContext('2d').clearRect(0, 0, 640, 640);
            tempCanvas.getContext('2d').putImageData(imgData, 0, 0);
          }
        }
      });

      currentCanvas.addEventListener('mousemove', (e) => {
        currentColor = newWindow.getComputedStyle(colorCircle).backgroundColor;
        const scale = 64 / canvasSize;
        if (newWindow.document.body.className.includes('cursor-pen') || newWindow.document.body.className.includes('cursor-eraser')) {
          if (isDrawing === true) {
            context.beginPath();
            context.rect(x, y, 10 * scale, 10 * scale);
            if (newWindow.document.body.className.includes('cursor-pen')) {
              context.globalCompositeOperation = 'source-over';
              context.fillStyle = currentColor;
            } else {
              context.globalCompositeOperation = 'destination-out';
              context.fillStyle = 'rgba(255,0,0,0.5);';
            }
            context.fill();
          }
          x = e.clientX - rect.left;
          y = e.clientY - rect.top + imgPen.height;
          x = Math.floor(x / 10) * 10;
          y = Math.floor(y / 10) * 10;
        } else if (newWindow.document.body.className.includes('cursor-line') && isDrawing === true) {
          x = e.clientX - rect.left;
          y = e.clientY - rect.top + imgPen.height;
          x = Math.floor(x / 10) * 10;
          y = Math.floor(y / 10) * 10;
          const imgData = tempCanvas.getContext('2d').getImageData(0, 0, 640, 640);
          context.clearRect(0, 0, 640, 640);
          context.putImageData(imgData, 0, 0);
          context.beginPath();
          context.moveTo(mouseDownX, mouseDownY);
          context.lineWidth = 640 / canvasSize;
          window.console.log('moved to', x, y);
          context.strokeStyle = currentColor;
          context.lineTo(x, y);
          context.stroke();
        } else if (newWindow.document.body.className.includes('cursor-rectangle') && isDrawing === true) {
          x = e.clientX - rect.left;
          y = e.clientY - rect.top + imgPen.height;
          x = Math.floor(x / 10) * 10;
          y = Math.floor(y / 10) * 10;
          const imgData = tempCanvas.getContext('2d').getImageData(0, 0, 640, 640);
          context.clearRect(0, 0, 640, 640);
          context.putImageData(imgData, 0, 0);
          context.beginPath();
          context.moveTo(mouseDownX, mouseDownY);
          context.lineWidth = 640 / canvasSize;
          window.console.log('moved to', x, y);
          context.strokeStyle = currentColor;
          context.strokeRect(Math.min(x, mouseDownX), Math.min(y, mouseDownY), Math.abs(x - mouseDownX), Math.abs(y - mouseDownY));
          context.stroke();
        }
      });

      currentCanvas.addEventListener('mouseup', () => {
        currentColor = newWindow.getComputedStyle(colorCircle).backgroundColor;
        const scale = 64 / canvasSize;
        if (newWindow.document.body.className.includes('cursor-pen') || newWindow.document.body.className.includes('cursor-eraser')) {
          if (isDrawing === true) {
            context.beginPath();
            context.rect(x, y, 10 * scale, 10 * scale);
            if (newWindow.document.body.className.includes('cursor-pen')) {
              context.globalCompositeOperation = 'source-over';
              context.fillStyle = currentColor;
            } else {
              context.globalCompositeOperation = 'destination-out';
              context.fillStyle = 'rgba(255,0,0,0.5);';
            }
            context.fill();
          }
        } else if (newWindow.document.body.className.includes('cursor-line')) {
          context.lineTo(x, y);
          context.closePath();
        }
        x = 0;
        y = 0;
        isDrawing = false;
        const currentFrameCanvas = newWindow.document.getElementById(`canvas${currentFrame}`);
        const currentFrameCanvasContext = currentFrameCanvas.getContext('2d');
        currentFrameCanvasContext.clearRect(0, 0, currentFrameCanvas.width, currentFrameCanvas.height);
        currentFrameCanvasContext.drawImage(currentCanvas, 0, 0, currentFrameCanvas.width, currentFrameCanvas.height);
      });
    }

    // currentCanvasHandlers();

    // handler for selecting new frame
    function changeHandler(event) {
      let target = event.currentTarget;
      while (!target.classList.contains('frame-item')) {
        target = target.parentNode;
      }
      checkFrames(target.id.slice(-1));
      currentFrame = target.id.slice(-1);
      currentCanvas = newWindow.document.getElementById(`canvas-main${target.id.slice(-1)}`);
      if (!currentCanvas) {
        currentCanvas = newWindow.document.createElement('canvas');
        currentCanvas.className = `canvas-main canvas-main${target.id.slice(-1)}`;
        currentCanvas.id = `canvas-main${target.id.slice(-1)}`;
        canvasDiv.appendChild(currentCanvas);
        const currentCanvasContext = currentCanvas.getContext('2d');
        const scale = previousCanvasSize / canvasSize;
        currentCanvasContext.scale(scale, scale);
      }
      context = currentCanvas.getContext('2d');
      rect = currentCanvas.getBoundingClientRect();
      changeMainCanvasVisibility();
      currentCanvasHandlers();
    }

    function drag(el) {
      const event = el.target.closest('.frame-item');
      dragedFrameId = event.id.slice(-1);
    }

    function allowDrop(el) {
      el.preventDefault();
    }

    function drop(el) {
      el.preventDefault();
      const dropTarget = el.target.closest('.frame-item');
      const dropCanvas = newWindow.document.getElementById(`canvas${dropTarget.id.slice(-1)}`);
      const dropMainCanvas = newWindow.document.getElementById(`canvas-main${dropTarget.id.slice(-1)}`);
      const dragCanvas = newWindow.document.getElementById(`canvas${dragedFrameId}`);
      const dragMainCanvas = newWindow.document.getElementById(`canvas-main${dragedFrameId}`);
      const dragTarget = newWindow.document.getElementById(`frame-item${dragedFrameId}`);
      domFrames[dropCanvas.id.slice(-1) - 1] = dragCanvas;
      domFrames[dragCanvas.id.slice(-1) - 1] = dropCanvas;
      domCanvas[dropMainCanvas.id.slice(-1) - 1] = dragMainCanvas;
      domCanvas[dragMainCanvas.id.slice(-1) - 1] = dropMainCanvas;
      dropMainCanvas.id = `canvas-main${dragedFrameId}`;
      dragMainCanvas.id = `canvas-main${dropTarget.id.slice(-1)}`;
      const tmp = newWindow.document.createElement('span');
      tmp.className = 'hide';
      dropTarget.before(tmp);
      dragTarget.before(dropTarget);
      tmp.replaceWith(dragTarget);
      updateFrames();
    }


    // handlers for deleting and drag-drop frame
    function instrumentsHandler() {
      newWindow.document.querySelectorAll('.delete-img').forEach((el) => {
        el.addEventListener('click', deleteHandler);
        el.addEventListener('mouseenter', () => {
          el.style.backgroundColor = 'rgb(214, 214, 214)';
        });
        el.addEventListener('mouseleave', () => {
          el.style.backgroundColor = '';
        });
      });

      newWindow.document.querySelectorAll('.drag-img').forEach((el) => {
        el.addEventListener('dragstart', drag);
        el.addEventListener('dragover', allowDrop);
        el.addEventListener('drop', drop);
        el.addEventListener('mouseenter', () => {
          el.style.backgroundColor = 'rgb(214, 214, 214)';
        });
        el.addEventListener('mouseleave', () => {
          el.style.backgroundColor = '';
        });
      });
    }

    // function for copying frame and canvas
    function copyHandler(event) {
      count += 1;
      let target;
      if (!event) {
        target = newWindow.document.getElementById(`frame-item${currentFrame}`);
      } else {
        target = event.currentTarget;

        while (!target.classList.contains('frame-item')) {
          target = target.parentNode;
        }
      }
      // copying frame
      const parent = target.parentNode;
      const cloneFrame = target.cloneNode(true);
      parent.appendChild(cloneFrame);
      cloneFrame.id = `frame-item${count}`;
      const instrument = cloneFrame.getElementsByClassName('instruments')[0];
      instrument.id = `instruments${count}`;
      instrument.childNodes[0].innerHTML = count;
      instrument.getElementsByClassName('copy-img')[0].style.backgroundColor = '';
      // change copying canvas in frame
      const curFrameCanvas = target.getElementsByClassName('canvas-frame')[0];
      const cloneframeCanvas = cloneFrame.getElementsByClassName('canvas-frame')[0];
      cloneframeCanvas.id = `canvas${count}`;
      const cloneframeCanvasContext = cloneframeCanvas.getContext('2d');
      cloneframeCanvasContext.drawImage(curFrameCanvas, 0, 0);
      domFrames.push(cloneframeCanvas);
      checkFrames(count);
      // copying main canvas
      const copyCanvas = currentCanvas.cloneNode();
      const contextCopyCanvas = copyCanvas.getContext('2d');
      contextCopyCanvas.drawImage(currentCanvas, 0, 0);
      copyCanvas.id = `canvas-main${count}`;
      copyCanvas.className.replace(`/ *\b${target.id.slice(-1)}\b/g`, count);
      canvasDiv.appendChild(copyCanvas);
      currentCanvas = copyCanvas;
      context = currentCanvas.getContext('2d');
      domCanvas.push(copyCanvas);
      changeMainCanvasVisibility();
      event.stopPropagation();

      newWindow.document.querySelectorAll('.copy-img').forEach((el) => {
        el.removeEventListener('click', copyHandler);
        el.addEventListener('click', copyHandler);
        el.addEventListener('mouseenter', () => {
          el.style.backgroundColor = 'rgb(214, 214, 214)';
        });
        el.addEventListener('mouseleave', () => {
          el.style.backgroundColor = '';
        });
      });
      newWindow.document.querySelectorAll('.frame-item').forEach((el) => {
        el.removeEventListener('click', changeHandler);
        el.addEventListener('click', changeHandler);
        el.addEventListener('mouseenter', () => {
          el.classList.add('mouseover-frame');
        });
        el.addEventListener('mouseleave', () => {
          el.classList.remove('mouseover-frame');
        });
      });
      instrumentsHandler();
    }

    // full-size animation
    newWindow.document.getElementById('canvas-player').addEventListener('click', (event) => {
      event.currentTarget.requestFullscreen();
    });

    // change animation speed
    rangeSizeCanvas.addEventListener('input', () => {
      fps = rangeSizeCanvas.value;
      clearInterval(intervalID);
      startAnimation(Number(fps));
    });

    // adding new frame
    function plusFrame() {
      canvasPlayer.style.height = `${canvasPlayer.clientWidth}px`;
      count += 1;
      that.view.addFrame(count, domFrames);
      checkFrames(count);

      currentCanvas = newWindow.document.createElement('canvas');
      currentCanvas.className = `canvas-main canvas-main${count}`;
      currentCanvas.id = `canvas-main${count}`;
      currentCanvas.style.zIndex = 1;
      currentCanvas.width = 640;
      currentCanvas.height = 640;
      canvasDiv.appendChild(currentCanvas);
      const currentCanvasContext = currentCanvas.getContext('2d');
      context = currentCanvasContext;
      rect = currentCanvas.getBoundingClientRect();
      domCanvas.push(currentCanvas);

      changeMainCanvasVisibility();
      clearInterval(intervalID);
      startAnimation(5);

      currentCanvasHandlers();

      newWindow.document.querySelectorAll('.copy-img').forEach((el) => {
        el.removeEventListener('click', copyHandler);
        el.addEventListener('click', copyHandler);
        el.addEventListener('mouseenter', () => {
          el.style.backgroundColor = 'rgb(214, 214, 214)';
        });
        el.addEventListener('mouseleave', () => {
          el.style.backgroundColor = '';
        });
      });
      newWindow.document.querySelectorAll('.frame-item').forEach((el) => {
        el.removeEventListener('click', changeHandler);
        el.addEventListener('click', changeHandler);
        el.addEventListener('mouseenter', () => {
          el.classList.add('mouseover-frame');
        });
        el.addEventListener('mouseleave', () => {
          // el.style.backgroundColor = '';
          el.classList.remove('mouseover-frame');
        });
      });
      instrumentsHandler();
    }

    plusFrame();

    leftPanel.addEventListener('mousedown', leftPanelEventClick);
    leftPanel.addEventListener('mouseover', leftPanelEventMouseOver);
    leftPanel.addEventListener('mouseout', leftPanelEventMouseOut);

    addFrame.addEventListener('click', plusFrame);
    addFrame.addEventListener('mouseenter', () => {
      addFrame.style.border = '3px solid grey';
    });


    addFrame.addEventListener('mouseleave', () => {
      addFrame.style.border = '1px solid grey';
    });

    exportImg.addEventListener('mouseover', (el) => {
      el.target.style.backgroundColor = 'rgb(214, 214, 214)';
    });
    exportImg.addEventListener('mouseout', (el) => {
      el.target.style.backgroundColor = '';
    });

    exportImg.addEventListener('click', () => {
      const gif = new GIF({
        workers: 2,
        quality: 10,
        workerScript: 'src/controller/gif.worker.js',
        background: '#fff',
        width: 640,
        height: 640,
      });

      domCanvas.forEach((item) => {
        gif.addFrame(item, { delay: 200 });
      });

      gif.on('finished', (blob) => {
        newWindow.open(URL.createObjectURL(blob));
      });

      gif.render();
    });

    newWindow.addEventListener('beforeunload', () => {
      const serialObj = JSON.stringify(User);
      localStorage.setItem(localStorage.email, serialObj);
      delete localStorage.email;
    });

    newWindow.document.addEventListener('keydown', (e) => {
      /* if (e.keyCode === 27) { // escape
        // cancel();
        return false;
      } */


      if ((e.shiftKey && e.keyCode === 'A'.charCodeAt(0))) {
        e.preventDefault();
        copyHandler();
        return false;
      }

      if ((e.shiftKey && e.keyCode === 'C'.charCodeAt(0))) {
        e.preventDefault();
        copyHandler();
        return false;
      }

      if ((e.shiftKey && e.keyCode === 'D'.charCodeAt(0))) {
        e.preventDefault();
        deleteHandler();
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'G'.charCodeAt(0))) {
        e.preventDefault();
        exportImg.focus();
        exportImg.click();
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'R'.charCodeAt(0))) {
        e.preventDefault();
        rangeSizeCanvas.focus();
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'S'.charCodeAt(0))) {
        e.preventDefault();
        selectCanvasSize[0].focus();
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'L'.charCodeAt(0))) {
        e.preventDefault();
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-line');
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'V'.charCodeAt(0))) {
        e.preventDefault();
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-rectangle');
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'B'.charCodeAt(0))) {
        e.preventDefault();
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-bucket');
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'C'.charCodeAt(0))) {
        e.preventDefault();
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-color-picker');
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'P'.charCodeAt(0))) {
        e.preventDefault();
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-pen');
        return false;
      }

      if ((e.ctrlKey && e.keyCode === 'E'.charCodeAt(0))) {
        e.preventDefault();
        newWindow.document.body.classList.remove('cursor-bucket', 'cursor-line', 'cursor-color-picker', 'cursor-rectangle', 'cursor-pen', 'cursor-eraser');
        newWindow.document.body.classList.add('cursor-eraser');
        return false;
      }
      return false;
    });
  }
}
